*{
  font-family: "verdana";
  font-size: 14px;
  margin: 0;
}

h1{
  font-size: 21px;
}

.App {
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.fallbackContainer{
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("https://anucde.org/img4.e0531e20a9f75722.jpg");
  height: 100vh;
  background-size: cover;
}